import React, { useState, useEffect } from 'react';
import { Form, Input, Button, Select } from 'antd';
import './search.scss'
const { Option } = Select;

const Search = (props: any) => {
    const [form] = Form.useForm();
    const [, forceupdate] = useState();

    useEffect(() => forceupdate([]), []);

    const onFinish = (values: any) => {
        console.log('Finish:', values);
        props.handleSearch(values)

    }

    const onTypeChange = (newType: any) => {
        console.log(newType);

    }

    return (
        <div >
            <Form form={form} name="horizontal_search" onFinish={onFinish} className='article_search'>

                <Form.Item
                    label="ID"
                    name="articleId"
                // rules
                >
                    <Input placeholder='ID' />
                </Form.Item>
                <Form.Item
                    name="searchContent"
                    label=""
                // rules
                >
                    <Input placeholder='' />
                </Form.Item>
                <Form.Item
                    label=""
                    name="userName"
                // rules
                >
                    <Input placeholder='' />
                </Form.Item>
                <Form.Item
                    label="ID"
                    name="userId"
                // rules
                >
                    <Input placeholder='ID' />
                </Form.Item>
                <Form.Item
                    name="articleTypeId"
                    label=""
                // rules
                >
                    <Select style={{ width: 150 }} defaultValue="0" onChange={onTypeChange}>
                        <Option value='0'></Option>
                        <Option value="1"></Option>
                        <Option value="2"></Option>
                        <Option value="3"></Option>
                    </Select>
                </Form.Item>

                <Form.Item shouldUpdate={true} className='search_btns'>

                    <Button type="primary" htmlType="submit" >  </Button>
                    <Button></Button>
                </Form.Item>
            </Form>
        </div>
    )
}

export default Search;